import React from 'react'
import { NavLink } from 'react-router-dom';
//import img1 from '../src/web/img1.png'

export default function Common(props) {
  return (
    <>
      <section id='header' className='d-flex align-items-center'>
        <div className='headerClass container-fluid  ' >
          <div className="row">
            <div className='col-10 mx-auto'>
              <div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 justify-content-center '>
                <h1>
                  {props.name} <strong className='brand-name'> {props.brandName} </strong>
                </h1>
                <h2 className='my-3'>
                  {props.title}
                </h2>
                <div className="mt-3">
                  <NavLink to={props.visitTo} className='btn btn-outline-primary'> 
                     {props.visitClick}
                  </NavLink>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className= ' header-img'>
          <img src={props.imgSrc} alt="home" />
        </div>
      </section>
    </>
  )
}

