import { click } from '@testing-library/user-event/dist/click'
import React, { useState } from 'react'

export default function ContactUs() {
  const [data , setData] = useState({
    fullName:"",
    phone: "",
    password:"",
    email: "",
    msg: ""
  });

  const change =(e)=>{
    const {name , value} = e.target;

    setData((prevData)=>{
      return{
        ...prevData,
        [name]:[value]
      };
    })
  }
  const formSubmit =(e)=>{
    e.preventDefault();
    alert(`Your name is ${data.fullName},
           Your email id- ${data.email},
           your contact number- ${data.phone},
           Your passWord- ${data.password},
           Your response- ${data.msg}`);

    console.log(`Your name is ${data.fullName},
    Your email id- ${data.email},
    your contact number- ${data.phone},
    Your passWord- ${data.password},
    Your response- ${data.msg}`)       
  }
  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className='col-md-6 col-10 mx-auto' >
            <form onSubmit={formSubmit}>
              <div className="form-group">
                <input type="name" 
                className="form-control" 
                id="exampleInputEmail1" 
                aria-describedby="emailHelp" 
                name='fullName'
                value={data.fullName}
                onChange={change}
                placeholder="Enter name" />
                <br />
                <input type="email" className="form-control" 
                id="exampleInputEmail1" 
                name='email'
                value={data.email}
                onChange={change}
                aria-describedby="emailHelp" placeholder="Enter email" />  
                <br />
              </div>
              <div className="form-group">
                <input type="password" 
                 className="form-control"
                 id="exampleInputPassword1" 
                 name='password'
                 value={data.password}
                 onChange={change}
                 placeholder="Password" />
              </div>
              <br />
              <div className="form-group">
               <input type="number" className="form-control" 
                id="exampleInputPassword1" 
                name='phone'
                value={data.phone}
                onChange={change}
                placeholder="Your Contact number" />
              </div>
              <br />
              <div className="form-group">
                <textarea type="text" className="form-control" 
                 id="exampleInputPassword1" 
                 name='msg'
                 value={data.msg}
                 onChange={change}
                 placeholder="Enter your suggesion" />
              </div>
              <br />
              
              <button  type="submit" 
               className="btn btn-outline-primary">Submit</button>
            </form>
          </div>
        </div>
      </div>
    </>
  )
}

