import React from 'react'
import img1 from '../src/web/img1.png' 
import { NavLink } from 'react-router-dom'
export default function Card(props) {
  return (
    <>
     
          
              <div className="col-md-4 col-10 mx-auto">
                <div className="card" style={{width: "14rem", height:"24rem"}}>
                  <img src={props.imgSrc} className="card-img-top" alt="..." />
                  <div className="card-body">
                    <h5 className="card-title" style={{color:" #275a86"}}>{props.title}</h5>
                    <p className="card-text"> {props.text} </p>
                    <NavLink to={props.visitTo} className="btn btn-outline-primary"> {props.visitText} </NavLink>
                  </div>
                </div>
              
          </div>
        
      
    </>
  )
}
